import vim_bindings from '!!raw-loader!./vim';
import page from '!!raw-loader!./page';
import { inject_script } from './inject';
import styles from './styles';
chrome.storage.local.get(['vim_disable_keys', 'default_disable_keys', 'theme', 'keyMap'], function (results) {
  inject_script("var __mapName = \"".concat(results.keyMap, "\";\nvar __vim_disable_keys = ").concat(results.vim_disable_keys || '', ";\nvar __default_disable_keys = ").concat(results.default_disable_keys || '', ";\nvar __addKeyMap = ").concat(vim_bindings, ";\nvar __styleName = \"").concat(results.theme in styles ? results.theme : 'deault', "\"\nvar __styleCSS = `").concat(results.theme in styles ? styles[results.theme] : '', "`\n").concat(page));
});